$light: #f7f7f7;
$dark: #222121;
$lime: #00cc66;
$blue: #2541b2;
$orange: rgb(238, 167, 36);
$lightblue:#465BF0;
$fresh:#3CC47C;
$vermillion:#FC4A1A;


.game-container {
display: flex;
align-items: center;
justify-content: center;
width: auto;

}

.gameResult {
  justify-content: center;
  align-items: center;
  display: inline-block;
  text-align: center;
  display: block;
}

h1 {
  color:$lime;
  font-weight: bold;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrong-guesses {
  display: flex;
  float: right;
  font-family:'Times New Roman', Times, serif;
  font-size:1.3rem;
  margin-right: 1rem;
}

.gameState {
 text-align: center;
 align-items: center;
 justify-content: center;
 background-color: transparent;
 border-top:transparent;
 border-left:transparent;
 border-right:transparent;
 cursor: pointer;
 font-size: 1.5rem;
 font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
 color: $vermillion;
}


.guessWord-container{
  display: block;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 1.4rem;
}

.popup {
  display:flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  #reset{
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: $lime;
  position: absolute;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  font-size: 1.5rem;
  border: transparent;
  cursor: pointer;
  }

}